import React from 'react'
import Title from '../components/Title'
import ItemPorfolio from '../components/ItemPorfolio'

const Works = [
  {
    name: "Jharm Wat",
    type: "Android Application",
    image: "/assets/image/work/m-1.jpg"
  },
  {
    name: "Mome (Dynamic form)",
    type: "Android Application",
    image: "/assets/image/work/m-2.jpg"
  },
  {
    name: "KKU Meeting",
    type: "Android Application",
    image: "/assets/image/work/m-3.jpg"
  },
  {
    name: "Smart hotel",
    type: "React Native",
    image: "/assets/image/work/m-4.jpg"
  },
  {
    name: "Marginframe",
    type: "React.js",
    image: "/assets/image/work/w-1.jpg"
  },
  {
    name: "Onespace",
    type: "React.js",
    image: "/assets/image/work/w-2.jpg"
  }
]

export default function Portfolio() {
  return (
    <div className="flex flex-col my-6">
      <div className="flex flex-row justify-center pt-2 pb-4">
        <Title text="Portfolio" />
      </div>
      <div className="flex grid flex-row flex-wrap grid-cols-1 gap-3 lg:grid-cols-2">
        {
          Works.map((work, index) => {
            return (
              <ItemPorfolio key={index} work={work} />
            )
          })
        }
      </div>
    </div>
  )
}
